import Slider from 'react-slick';
import { Link } from 'react-router-dom';
import { useTranslation } from "react-i18next";

export function Roadmap() {
   const { t } = useTranslation();

   const roadmapItems = {
      slidesToShow: 7,
      slidesToScroll: 1,
      infinite: false,
      arrows: false,
      variableWidth: true,
      touchThreshold: 10
   }

   const roadmapCards = [
      {
         "img_url": "images/media_1.png",
         "description": t("block-6.card-text-1"),
         "num_1": "Q1",
         "num_2": "22"
      },
      {
         "status": "ready",
         "description": t("block-6.card-text-2"),
         "num_1": "Q2",
         "num_2": "22"
      },
      {
         "status": "pre",
         "description": t("block-6.card-text-3"),
         "num_1": "Q3",
         "num_2": "22"
      },
      {
         "status": "process",
         "description": t("block-6.card-text-4"),
         "num_1": "Q4",
         "num_2": "22"
      },
      {
         "status": "process",
         "description": t("block-6.card-text-5"),
         "num_1": "Q5",
         "num_2": "22"
      },
      {
         "status": "process",
         "description": t("block-6.card-text-6"),
         "num_1": "Q6",
         "num_2": "22"
      },
      {
         "status": "process",
         "description": t("block-6.card-text-7"),
         "num_1": "Q7",
         "num_2": "22"
      },
      {
         "status": "process",
         "description": t("block-6.card-text-8"),
         "num_1": "Q8",
         "num_2": "22"
      },
      {
         "status": "process",
         "description": t("block-6.card-text-9"),
         "num_1": "Q9",
         "num_2": "22"
      },
      {
         "status": "process",
         "description": t("block-6.card-text-10"),
         "num_1": "Q10",
         "num_2": "22"
      },
      {
         "status": "process",
         "description": t("block-6.card-text-11"),
         "num_1": "Q11",
         "num_2": "22"
      }
   ]

   return (
      <section className='roadmap' id="roadmap">
         <div className='roadmap__body'>
            <h2 className='roadmap__title'>{t("block-6.title")}</h2>
            <div className='roadmap__items'>
               <Slider {...roadmapItems}>

                  {roadmapCards.map((item, index) => (
                     <div className='roadmap__item' key={index}>
                        <div className='roadmap__item-body'>
                           <p className='roadmap__item-text'>{item.description}</p>
                           <div className='roadmap__item-image'>
                              {item.img_url ? <img src={item.img_url} alt="image" /> : ''}
                           </div>
                           <div className='roadmap__item-count'>
                              <span className='num-1'>{item.num_1}</span>
                              <span className='divider'>/</span>
                              <span className='num-2'>{item.num_2}</span>
                           </div>
                        </div>
                     </div>
                  ))}

               </Slider>
            </div>
            <div className="roadmap__button">
               <Link to="/presale">{t("block-6.button-text")}</Link>
            </div>
         </div>
      </section>
   );
}